import React, { forwardRef, HTMLAttributes, ReactElement, ReactNode } from 'react';
import { css } from '@emotion/react';
import styled from '@emotion/styled';

import { base, colors, h5 } from '../theme';
import { AlignmentPrimaryType, DirectionType } from '../utils/types';
import { Order } from './indicator';
import { Flex } from './layout';
import { Text } from './Text';

type AlignmentType = 'inherit' | 'baseline' | 'bottom' | 'middle' | 'top';

interface TdProps extends HTMLAttributes<HTMLTableCellElement> {
  /**
   * Alignment, secondary axis
   */
  align?: AlignmentType;
  /**
   * Children
   */
  children?: ReactNode;
  /**
   * Alignment, primary axis
   */
  justify?: AlignmentPrimaryType;
  /**
   * Numberic
   */
  numeric?: 'normal' | 'slashed-zero' | 'tabular-nums';
  /**
   * Size
   */
  size?: string;
}

/**
 * Table data cell
 */
export const Td = forwardRef<HTMLTableCellElement, TdProps>(
  ({ align, justify, numeric = 'normal', ...props }, ref): JSX.Element => (
    <TdContainer
      data-align={align || null}
      data-justify={justify || null}
      data-numeric={numeric || null}
      ref={ref}
      {...props}
    />
  )
);

const textAlign = css`
  &[data-justify='inherit'] {
    text-align: inherit;
  }
  &[data-justify='start'] {
    text-align: start;
  }
  &[data-justify='center'] {
    text-align: center;
  }
  &[data-justify='end'] {
    text-align: end;
  }

  &[data-numeric='slashed-zero'] {
    font-variant-numeric: slashed-zero;
  }
  &[data-numeric='tabular-nums'] {
    font-variant-numeric: tabular-nums;
  }
`;

const TdContainer = styled.td<TdProps>`
  ${textAlign};

  ${({ size }) => size && `width: ${size};`}

  &[data-align='baseline'] {
    vertical-align: baseline;
  }
  &[data-align='bottom'] {
    vertical-align: bottom;
  }
  &[data-align='middle'] {
    vertical-align: middle;
  }
  &[data-align='top'] {
    vertical-align: top;
  }
`;

interface ThProps extends HTMLAttributes<HTMLTableCellElement> {
  /**
   * Alignment, secondary axis
   */
  align?: AlignmentType;
  /**
   * Children
   */
  children?: ReactNode;
  /**
   * Order direction
   */
  direction?: DirectionType;
  /**
   * Icon
   */
  icon?: ReactElement;
  /**
   * Alignment, primary axis
   */
  justify?: AlignmentPrimaryType;
  /**
   * Size
   */
  size?: string;
  /**
   * Sortable
   */
  sortable?: boolean;

  /**
   * Event: Click
   */
  onClick?: () => void;
}

/**
 * Table header cell
 */
export const Th = forwardRef<HTMLTableCellElement, ThProps>(
  (
    { align, children, direction, icon, justify, onClick, sortable, ...props },
    ref
  ): JSX.Element => (
    <ThContainer data-align={align || null} data-justify={justify || null} ref={ref} {...props}>
      <Flex gap="4px" data-sortable={sortable || null} onClick={onClick}>
        {icon}
        <Text truncate>{children}</Text>
        {sortable && <Order direction={direction} />}
      </Flex>
    </ThContainer>
  )
);

const ThContainer = styled.th<ThProps>`
  ${textAlign};

  ${({ size }) => size && `width: ${size};`}

  [data-sortable] {
    cursor: pointer;
    user-select: none;
  }
  & > div {
    ${h5};
  }
`;

export interface TrProps extends HTMLAttributes<HTMLTableRowElement> {
  /**
   * Children
   */
  children: ReactNode;
  /**
   * Muted
   */
  mute?: boolean;
}

/**
 * Table row
 */
export const Tr = forwardRef<HTMLTableRowElement, TrProps>(
  ({ mute, ...props }, ref): JSX.Element => (
    <TrContainer data-mute={mute || null} ref={ref} {...props} />
  )
);

export const TrContainer = styled.tr`
  &[data-mute] {
    td,
    th {
      color: ${colors.TEXT_TERTIARY};
    }
  }
`;

/**
 * Table head
 */
export const Thead = forwardRef<HTMLTableSectionElement, { children: ReactNode }>(
  (props, ref): JSX.Element => <TheadContainer ref={ref} {...props} />
);

const TheadContainer = styled.thead``;

/**
 * Table body
 */
export const Tbody = forwardRef<HTMLTableSectionElement, { children: ReactNode }>(
  (props, ref): JSX.Element => <TbodyContainer ref={ref} {...props} />
);

const TbodyContainer = styled.tbody``;

export interface TableProps extends HTMLAttributes<HTMLTableElement> {
  /**
   * Children
   */
  children: ReactNode;
  /**
   * Layout
   */
  layout?: 'auto' | 'fixed';
  /**
   * Size
   */
  size?: string;
  /**
   * Sticky header
   */
  sticky?: boolean;
}

/**
 * Table
 */
export const Table = forwardRef<HTMLTableElement, TableProps>(
  ({ layout, sticky, ...props }, ref): JSX.Element => (
    <TableContainer
      data-layout={layout || null}
      data-sticky={sticky || null}
      ref={ref}
      {...props}
    />
  )
);

const TableContainer = styled.table<TableProps>`
  ${base};

  --background-color: ${colors.WHITE};
  --border-color: ${colors.STROKE};
  --border-size: 1px;
  --font-size: 14px;
  --font-weight: 600;
  --indent: 8px;
  --line-height: 16px;

  background-color: var(--background-color);
  border-collapse: collapse;
  border-spacing: 0;

  &[data-layout='fixed'] {
    table-layout: fixed;
  }

  &[data-sticky] {
    thead {
      &,
      & tr,
      & th {
        background: inherit;
      }
      th {
        position: sticky;
        top: 0;
        z-index: 2;

        &::after {
          border-bottom: var(--border-size) solid var(--border-color);
          content: '';
          inset: auto 0 calc(-1 * var(--border-size)) 0;
          position: absolute;
        }
      }
    }
  }

  ${({ size }) => `width: ${size ?? '100%'};`}

  tr {
    border-bottom: var(--border-size) solid var(--border-color);

    th,
    td {
      height: calc(48px - 1px);

      &:first-of-type {
        padding-left: calc(2 * var(--indent));
      }
      &:last-of-type {
        padding-right: calc(2 * var(--indent));
      }
    }
  }

  th,
  td {
    padding: var(--indent);
    text-align: start;
  }
`;
